body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.n-card{
  margin: 20px;
}

.n-card-image{ 
  display: flex;
  justify-content: center;  
  border-radius: 10px;
  width: 300px;
  height: 250px;
  box-shadow: 0px 0px 3px 1px #ccc;
  margin: 20px;
}

.n-card-info{
  /* border: solid 1px blue; */
  min-height: 100px;
  width: auto;
  padding-left: 5px ;

}

.n-card-title{
  font-size: 24px;
  margin: 0px;
}

.n-card-sub-title{
  font-size: 16px;
  margin: 0px;
}

.n-footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 4em;
  background-color: transparent;
}

.success-msg{
  color: #57A773;
  padding: 10px 15px;
}

.error-msg{
  color: #EE6352;
  padding: 10px 15px;
}

.navToggle{
  height: 100%;
}

.nav-arrow{
  z-index: 25;
  position : fixed;
  top: 90%;
}

.nav-arrow-right{
  right: 0;
}

.nav-arrow-left{
  left: 0;
}

/* tech-card */
.tech-card{
  display: flex;
  font-size: 0.8em;
  color: white;
  width: auto;
  height: 20px;
  padding: 1px 5px ;
  border: solid 1px blue;
  border-radius: 2px 5px 5px;
  background-color: rgb(93, 192, 223);
  background-color: rgba(93, 192, 223, 0.9);
}


/* stack game */
* {padding: 0; margin: 0;}
        canvas{ 
          background-color: white;
          display: block; 
          margin: 0 auto;
          border: thick double #32a1ce; 
        }

        .game-over-notify {
            position: fixed;
            display: none;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(255, 255, 255, 0.5);
            align-items: center;
            justify-content: center;
            font-family: sans-serif;
            font-size: 40px;
            font-weight: bold;
            color: red;
          }

          .win-notify {
            position: fixed;
            display: none;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(255, 255, 255, 0.5);
            align-items: center;
            justify-content: center;
            font-family: sans-serif;
            font-size: 40px;
            font-weight: bold;
            color: green;
          }

/* End stack game  */

/* canvas */
.canvas-img{
  display: none;
  background-color: transparent;
}


@media only screen and (max-width : 600px) {
  /* Styles */
  canvas{
    width: 100%;
  }
}

.formContact{
  margin-top: 100px;
}